<template>
  <div class="servie-wrapper">
    <c-menu :active="0"></c-menu>
    <c-tab :tabs="tabs" :active.sync="tabActiveIndex">
      <component :is="currentComponent" :category="category" :banner="banner"></component>
    </c-tab>
    <div class="add-message" v-if="tabActiveIndex === 0">
      <img src="@/assets/img/edit-article.png" />
      <div @click="openMessage">发帖</div>
    </div>
    <b-message ref="message"></b-message>
  </div>
</template>

<script>
import Menu from "@/components/comm/Header";
import Tab from "@/components/comm/Tab";
import Bmessage from "@/components/business/message";
import Resource from "./components/resource";
import Welfare from "./components/welfare";
import About from "./components/about";
/* eslint-disable */
export default {
  name: "ServiceIndex",
  components: {
    "c-menu": Menu,
    "c-tab": Tab,
    "b-message": Bmessage
  },
  data() {
    return {
      tabActiveIndex: 0,
      tabs: [
        {
          label: "每日福利"
        },
        {
          label: "卡券商城"
        },
        {
          label: "聚合联创"
        }
      ],
      category: [],
      banner: ""
    };
  },
  created() {
    this.getCategory();
  },
  computed: {
    currentComponent() {
      if (this.tabActiveIndex === 0) {
        return Resource;
      } else if (this.tabActiveIndex === 1) {
        return Welfare;
      } else if (this.tabActiveIndex == 2) {
        return About;
      }
      return Resource;
    }
  },
  methods: {
    getCategory() {
      this.$fetch.form("/Home/Api/index").then(({ data }) => {
        this.category = data.article_cls;
        this.banner = data.banner_data[0].shulf_src;
      });
    },
    openMessage() {
      this.$refs.message.open();
    }
  }
};
</script>

